@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// Prevent scrolling behind the modal
html.modal--active,
html.modal--active body {
  overflow: hidden;

  .navbar {
    z-index: 1;
  }
}

// Modal
.modal {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  transition: opacity 200ms;
  z-index: -1;

  > * {

    opacity: 0;
  }

  &:target,
  &.target {
    pointer-events: auto;
    opacity: 1;
    z-index: 9999;
    position: fixed;

    > * {

      opacity: 1;
    }
    
    > .modal__inner {

      animation: 1.25s ease-out modal-fade-up forwards;
    }
  }
}

.modal__inner {
  position: absolute;
  display: block;
  width: calc(100% - #{rem(16 + 16)});
  max-width: rem(320);
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: 0px 8px 20px 0px rgba(0,0,0,0.25);
  @include var(background,--colour-white);
  height: auto;
  max-height: calc(100vh - #{rem(16 + 16)});
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  @include media-breakpoint-up(sm) {

    max-width: rem(560);
  } 
  @include media-breakpoint-up(md) {

    max-width: rem(640);
  } 
}

.modal__header {
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);
  padding: rem(16) rem(48) rem(16) rem(16);
  text-align: left;
  @include var(background-color,--colour-light);
  position: relative;

  @include media-breakpoint-up(sm) {
    text-align: center;
    padding-left: rem(48);
    padding-right: rem(48);
  }

  @include media-breakpoint-up(md) {
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-right: 3rem;
    padding-left: 3rem;
    margin-left: 0;
  }

  .modal__close {
    
    position: absolute;
    top: 0;
    right: 0;
    padding: rem(20) rem(16);
    
    .svg__wrapper {
      margin: 0;
    }
  }
}

.modal__body {
  padding: rem(32) rem(16) 0 rem(16);
  @include var(background-color,--colour-white);
  text-align: center;

  @include media-breakpoint-up(sm) {
    padding-left: rem(48);
    padding-right: rem(48);
  }
}

.modal__overlay {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color:rgba(0,0,0,0.5);
  transition: opacity 200ms;
}

.modal--large {

  .modal__inner {
    
    @include media-breakpoint-up(sm) {
      
      max-width: rem(720);
    }
    @include media-breakpoint-up(md) {
      
      max-width: rem(1024);
    }
  
  }
}

.modal--image {

  .modal__inner {
    
    > figure {
      margin: 0;
    }
    > figure > img {
      object-fit: contain;
      margin: 0;
    }

    @include media-breakpoint-up(md) {
      
      width: fit-content;
      max-width: calc(100% - #{rem(96)});
    }
  
  }
}