.list-social {

  padding-bottom: rem(48);
  overflow: hidden;


  li {
    float: left;
    margin-left: 0;
    margin-right: rem(16);

    svg {
      height: rem(48);
      width: rem(48);
      fill: white;
    }

    svg .svg__border {
      display: none;
    }

  }

  @include invert-colours() {
    
    li {
      svg {
        fill: currentColor;
      }
  
      svg .svg__border {
        display: block;
      }
  
      a:not(.hover):not(.focus):not(.active) svg .svg__bg {
  
        display: none;
      }
    }
  }


}


// #region Vertical
@include media-breakpoint-up(md) {

  .list-social--vertical {

    max-width: rem(48 + 16);
    
    li {
      clear: both;
      margin-bottom: rem(16);
    }
  }
}
// #endregion