@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.footer {
  
  padding-top: rem(64);
  padding-bottom: rem(144);
  
  h3, .h3 {
    padding-bottom: rem(24);


    @include media-breakpoint-up(sm) {

      padding-bottom: rem(32);

    }
  }

  @include media-breakpoint-up(md) {

    padding-top: rem(96);
    padding-bottom: rem(200);
    min-height: rem(800);
  }

  [class*="col-"]{
    margin-bottom: rem(16);    
    
    @include media-breakpoint-up(sm) {

      margin-bottom: rem(32);
    }
  }

  &[class*="bg-"] .pattern__inner svg {
    stroke-width: 12px;
    transform: translate(0%, -100%);
    left: 0;
    opacity: 1;
    @include var(stroke,--colour-primary,!important);
  }

// IE11 fix 
  @if $compatible == 'true' {
  
    &:not([class*='stroke-']).bg-dark-2 .pattern__inner svg {
      stroke-width: 12px;
      transform: translate(0%, -100%);
      left: 0;
      @include var(stroke,--colour-primary,!important);
    }
  }

  
  .btn-link {
    margin-bottom: rem(24);
    text-decoration: none!important;
  }


  .list-social {

    padding-top: rem(16);
    padding-bottom: rem(48);

    li {

      margin-right: rem(8);

      svg {
        height: rem(32);
        width: rem(32);
      }
    }
  }
}

.footer--slim {
  padding-top: rem(40);
  padding-bottom: rem(32);
  
  @include media-breakpoint-up(md) {

    padding-top: rem(32);
    padding-bottom: rem(40);
    min-height: auto;

  }
    [class*="col-"]{
      margin-right: rem(16);    
      
      @include media-breakpoint-up(sm) {
  
        margin-right: rem(16);
      }
      @include media-breakpoint-up(md) {
  
        margin-right: rem(80);
      }
    }
}
