.pips {


  padding: rem(24) 0;

  ul{
      
    display:block;
    border: none;
    text-align: center;
    list-style: none;
    padding: 0;
    margin: 0 -#{rem(16)} 0 -#{rem(16)};
    justify-content: center;
    max-width: none;

    .pip {

      padding: 0;
      text-align: center;
      display: inline-block;
      min-width: auto;
      max-width: auto;
      margin: 0 rem(8) 0 rem(8);

      &:before {
        display: none;
      }

      a,
      button {
        display: inline-block;
        width: rem(12);
        height: rem(12);
        border-radius: 50%;
        border: solid 1px $colour-muted;
        padding: 0;
        background-color: transparent;

        &.current {
          @include var(border-color, --colour-primary);
          @include var(background, --colour-primary);

          &:before {
            display: none;
          }
        }

        &.focus {
          outline-width: 1px;
        }

        &.focus,
        &.hover {
          @include var(border-color, --colour-primary);

          &:before {
            display: none;
          }
        }
      }
    }
  }
}
