.tooltip {

  text-decoration-style: dotted!important;
  position: relative;
  font-weight: bold;
  font-variation-settings: 'wght' 600;
  cursor: pointer;

  &:not(.border-0) {
    text-decoration: underline;
    text-underline-offset: 0.5rem;
  }

  strong,
  .strong {
    color: inherit;
  }
  
  &:after {
    display: none;
  }

  &.hover,
  &.focus,
  &.active {
    
    @include var(color, --colour-hover);
    text-decoration-style: solid!important;
    text-decoration-thickness: 1px!important;
  }

  &.btn-link {
    margin-bottom: rem(32);
  }
}

@include inline-text(){
  
  .tooltip {
    margin: 0;
    display: inline;
    font-weight: 700;

    &:not(.border-0) {

      text-underline-offset: 0.25rem;
    }
  }
}

[data-tooltip] {

  font-size: rem(14);
  line-height: rem(24);
  font-weight: 400;
  @include var(font-family,--font-body);
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition:   opacity 0.2s ease-in-out,
                visibility 0.2s ease-in-out,
                margin 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);
                transform: translate3d(0, 0, 0);
  z-index: 2;
  padding: rem(12);
  margin-top: rem(8);
  width: rem(290);
  @include var(background-color, --colour-dark);
  @include var(color, --colour-white);
  
  a,
  a.hover,
  a.focus {
    @include var(color, --colour-white);
  }

  @include invert-colours(" .tooltip:not(.tooltip--no-invert)") {
    @include var(background-color,--colour-white);
    @include var(color,--colour-black);

    
    a {
      @include var(color, --colour-link);
    }

    a.hover,
    a.focus {
      @include var(color, --colour-hover);
    }
  }

  border-radius: rem(8);
  text-align: left;
  bottom: auto;
  top: 100%;
  left: 0;
  margin: rem(8) 0; 


  /* Show tooltip on hover */
  .tooltip.hover &,
  .tooltip.focus &,
  .tooltip.focus-within &,
  .tooltip.active & {
    visibility: visible;
    opacity: 1;
  }

  .tooltip--right & {
    left: auto;
    right: 0;
  }
  .tooltip--center & {
    left: 50%;
    transform: translate(-50%,0); 
  }

  // Tooltip top
  .tooltip--top & {
    top: auto;
    bottom: 100%;
  }

  @include media-breakpoint-up(sm) {
    .tooltip--sm-left & {
      left: 0;
      right: auto;
      transform: none;
    }
    .tooltip--sm-right & {
      left: auto;
      right: 0;
      transform: none;
    }
    .tooltip--sm-center & {
      right: auto;
      left: 50%;
      transform: translate(-50%,0); 
    }
  }
  @include media-breakpoint-up(md) {
    .tooltip--md-left & {
      left: 0;
      right: auto;
      transform: none;
    }
    .tooltip--md-right & {
      left: auto;
      right: 0;
      transform: none;
    }
    .tooltip--md-center & {
      right: auto;
      left: 50%;
      transform: translate(-50%,0); 
    }
  }
}


// Sizes ********************************************************************************************************************
.tooltip--short [data-tooltip] {
  width: rem(216);
}
@include media-breakpoint-up(sm) {
  [data-tooltip] {
    width: rem(320);
  }
  .tooltip--short [data-tooltip] {
    width: rem(216);
  }

  .tooltip--long [data-tooltip] {
    width: rem(480);
  }
}
