
$animateSelector: "body.animate";

@if $system == 'true' {
  
  $animateSelector: ".system-stage.animate";
}

$motionSelector: "*:not(.container):not(.pattern):not(.card-deck):not(.pattern__inner):not(.row):not(.tabpanel):not(.collapse):not(.collapsing)";

// Containers
#{$animateSelector} .container:not(.no-animate):not(.notification):not(.filter){

  &.inview > #{$motionSelector},
  &.inview .container:not(.pagination) > #{$motionSelector} {
    
    @include var(animation, --animation-fade-up);
  }

  &.not-inview > #{$motionSelector},
  &.not-inview .container:not(.pagination) > #{$motionSelector} {
    opacity: 0;
    transform: translate(0, $animation_offset);
  }

  // Row
  &.not-inview > .row > [class*="col"] > *:not(picture):not(img):not(.youtube-embed):not(.pattern__inner) {
    opacity: 0;
    transform: translate(0, $animation_offset);
  }

  &.inview > .row > [class*="col"] > *:not(picture):not(img):not(.youtube-embed):not(.pattern__inner) {
    opacity: 0;
    transform: translate(0, $animation_offset);
    @include var(animation, --animation-fade-up);
  }

  &.not-inview > .row > [class*="col"] > img,
  &.not-inview > .row > [class*="col"] > picture {
    opacity: 0;
  }

  &.inview > .row > [class*="col"] > img,
  &.inview > .row > [class*="col"] > picture {
    opacity: 0;
    @include var(animation, --animation-fade-in);
  }


  // Cards
  &.not-inview .card {
    transform: translate(0, $animation_offset);
    opacity: 0;
  }

  &.inview .card {
    transform: translate(0, $animation_offset);
    opacity: 0;
    @include var(animation, --animation-fade-up);
  }

  // tab-size
  &.not-inview .tabpanel.show-onload > #{$motionSelector},
  &.not-inview .tabpanel.show-onload > .pagination {
    opacity: 0;
    transform: translate(0, $animation_offset);
  }
  &.inview .tabpanel.show-onload > #{$motionSelector},
  &.inview .tabpanel.show-onload > .pagination {
    
    @include var(animation, --animation-fade-up);
  }
}


// Patterns
.pattern__inner {

  // Lengths worked out by running the below javascript in the console,
  // you will have to make sure the svg path is embedded in page and has the id of path
  // 
  // var myPath = document.getElementById("path");
  // var length = myPath.getTotalLength();
  // console.log(length);
  --pattern-1-length: 5786px;
  --pattern-2-length: 3817px;
  --pattern-3-length: 5033px;
  --pattern-giving-length: 781px;

  // The offset is worked out using trial and error
  // We assign an offset so that the animation starts at roughly where it would appear on screen
  // rather than off screen, so that we see the animation straight away.
  // The offset will be different depening upon the pattern and any transforms it has
  --pattern-1-offset: 1460px;
  --pattern-2-offset: 1000px;
  --pattern-3-offset: 1400px;
  --pattern-giving-offset: 0px;

  --pattern-1-animation-speed: 8s;
  --pattern-2-animation-speed: 6s;
  --pattern-3-animation-speed: 7s;
  --pattern-giving-animation-speed: 3s;

  --pattern-scale: 1;
  
  .pattern--lg & {

    --pattern-scale: 2;
  }

  .pattern--xl & {
    --pattern-scale: 2.5;
  }
  
  .pattern--xxl & {
    --pattern-scale: 3;
  }
  

  @include media-breakpoint-up(sm) {
    --pattern-1-offset: 1040px;
    --pattern-2-offset: 700px;
    --pattern-3-offset: 600px;
    --pattern-giving-offset: 0px;
  }

  @include media-breakpoint-up(md) {
    --pattern-1-offset: 290px;
    --pattern-2-offset: 100px;
    --pattern-3-offset: 70px;
    --pattern-giving-offset: 0px;
  }


  // Animation speed
  --pattern-offset: calc(var(--pattern-1-offset) * var(--pattern-scale));
  --pattern-length: calc(var(--pattern-1-length) * var(--pattern-scale));
  --pattern-animation-speed: calc(var(--pattern-1-animation-speed) * var(--pattern-scale));
  
  .pattern--2 & {
    --pattern-offset: calc(var(--pattern-2-offset) * var(--pattern-scale));
    --pattern-length: calc(var(--pattern-2-length) * var(--pattern-scale));
    --pattern-animation-speed: calc(var(--pattern-2-animation-speed) * var(--pattern-scale));  
  }

  .pattern--3 & {
    --pattern-offset: calc(var(--pattern-3-offset) * var(--pattern-scale));
    --pattern-length: calc(var(--pattern-3-length) * var(--pattern-scale));
    --pattern-animation-speed: calc(var(--pattern-3-animation-speed) * var(--pattern-scale));  
  }

  .pattern--giving & {
    --pattern-offset: calc(var(--pattern-giving-offset) * var(--pattern-scale));
    --pattern-length: calc(var(--pattern-giving-length) * var(--pattern-scale));
    --pattern-animation-speed: calc(var(--pattern-giving-animation-speed) * var(--pattern-scale));  
  }

  .pattern--xs & {
    --pattern-offset: 0px;
  }
  //Possibly being removed
  .pattern--sm & {
    --pattern-offset: 0px;
  }
  // Turn off the animation when requested via preferences
  @media (prefers-reduced-motion: reduce) {
    --pattern-animation-speed: 0!important;
  }
}

#{$animateSelector} .container:not(.no-animate){

  &.inview .pattern__inner svg,
  &.pattern--overlap-bottom.inview + .pattern--overlap-top .pattern__inner svg  {
    --animation-draw-in: var(--pattern-animation-speed) ease-out draw-in forwards;
    @include var(animation, --animation-draw-in);
  }

  // Motion *****************************************************************
  &.not-inview .pattern__inner svg {
    stroke-dashoffset: calc(var(--pattern-length) - var(--pattern-offset));
    stroke-dasharray: var(--pattern-length);
  }
}

// Animate pattern 

@if $system == 'false' {
  .pattern__inner {

    svg {
      stroke-dashoffset: calc(var(--pattern-length) - var(--pattern-offset));
      stroke-dasharray: var(--pattern-length);
    }
  }
      
  #{$animateSelector} .container.inview-onload,
  #{$animateSelector} .banner.inview-onload {

    .pattern__inner svg {
      --animation-draw-in: var(--pattern-animation-speed) ease-out draw-in 0.5s forwards;
      @include var(animation, --animation-draw-in);
    }
  }

  #{$animateSelector} .container.collapsing,
  #{$animateSelector} .container.collapse {
    .pattern__inner svg {
    animation: none;
    }
  }
}

@media (prefers-reduced-motion) {
  body * {
    animation: none!important;
  }
  body .pattern__inner svg {
    stroke-dasharray: 0;
  }
}
