// #region bootstrap 4.3.1 resets

.form-control {
  display: block;
  width: 100%;
  height: $input-height;
  padding: $input-padding-y $input-padding-x;
  font-family: $input-font-family;
  @include font-size($input-font-size);
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-bg;
  background-clip: padding-box;
  border: $input-border-width solid $input-border-color;

  // Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.
  @include border-radius($input-border-radius, 0);

  @include box-shadow($input-box-shadow);
  @include transition($input-transition);

  // Unstyle the caret on `<select>`s in IE10+.
  &::-ms-expand {
    background-color: transparent;
    border: 0;
  }

  // Remove select outline from select box in FF
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 $input-color;
  }

  // Customize the `.focus` state to imitate native WebKit styles.
  //@include form-control-focus($ignore-warning: true);

  // Placeholder
  &::placeholder {
    color: $input-placeholder-color;
    // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
    opacity: 1;
  }

  // Disabled and read-only inputs
  //
  // HTML5 says that controls under a fieldset > legend:first-child won't be
  // disabled if the fieldset is disabled. Due to implementation difficulty, we
  // don't honor that edge case; we style them as disabled anyway.
  &:disabled,
  &[readonly] {
    background-color: $input-disabled-bg;
    // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.
    opacity: 1;
  }
}

select.form-control {
  &.focus::-ms-value {
    // Suppress the nested default white text on blue background highlight given to
    // the selected option text when the (still closed) <select> receives focus
    // in IE and (under certain conditions) Edge, as it looks bad and cannot be made to
    // match the appearance of the native widget.
    // See https://github.com/twbs/bootstrap/issues/19398.
    color: $input-color;
    background-color: $input-bg;
  }
}

// Make file inputs better match text inputs by forcing them to new lines.
.form-control-file,
.form-control-range {
  display: block;
  width: 100%;
}

// #endregion

// #region Browser Fixes 
.js-enabled input[type=number]::-webkit-outer-spin-button,
.js-enabled input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.js-enabled input[type=number] {
    -moz-appearance:textfield;
}
// #endregion

// #region Label
.form-group__title {
  @include var(font-family, --font-heading);
  font-weight: 400;
  font-size: rem(20);
  line-height: rem(28);
  margin: 0;
  margin-bottom: rem(32);
  display: block;
}

label,
.label {
  
  @include var(font-family, --font-heading);
  font-weight: bold;
  font-variation-settings: 'wght' 400;
  font-size: rem(14);
  line-height: rem(24);
  margin: 0;
  margin-bottom: rem(8);
  display: block;

  @include media-breakpoint-up(sm) {

    font-size: rem(16);
  }

  .optional {

    margin-left: rem(8);
    @include var(color,--colour-muted);
  }

  // Hint test
  .hint {

    font-size: rem(14);
    line-height: rem(24);
    @include var(font-family,--font-body);
    font-weight: 400;
    font-variation-settings: 'wght' 300;

    @include invert-colours(){
      color: inherit;
    }

    display: block;
    padding-top: rem(8);
    padding-bottom: rem(8);
    @include var(color,--colour-muted);
  }

  // Hide error message unless user has submitted form
  .field-validation-error,
  .error {
    display: none;
  }
}
// #endregion

// #region .form-control - input fields
.form-control {
  height: var(--input-height);
  @if $compatible == 'true'{
    height: rem(24 + 12 + 12);
  }
  width: 100%;
  font-size: rem(14);
  line-height: rem(24);
  @include var(background-color, --colour-input);
  border: 1px solid currentColor;
  @include var(border-color, --colour-muted);
  padding: rem(8) rem(12);
  margin: 0;
  border-radius: 0;
  display: inline-block;
  
  @include media-breakpoint-up(sm) {
    
    font-size: rem(16);
    padding: rem(12) rem(16);
    @if $compatible == 'true'{
      height: rem(24 + 12 + 12);
    }
  }

  &.hover {
    @include var(border-color, --colour-body);
  }

  &:not(.focus):not(.active){
    box-shadow: none;
  }

  &.focus,
  &.focus-visible,
  &.active {
    @include var(border-color, --colour-body);
    box-shadow: 0 0 0 2px var(--colour-body);
    outline: none;

    @if $compatible == 'true' {

      box-shadow: 0 0 0 2px #{map-get($vars, --colour-body)};
    }
  }
  
  &[disabled] {

    @include var(background-color, --colour-light);
    @include var(border-color, --colour-border);
    pointer-events: none;
  }
}
// #endregion

// #region Form group
.form-group {

  position: relative;
  overflow: visible;
  //max-width: $max-content-width;
  @include clearfix();
  margin-bottom: rem(40);

  // Set an input line height to be used later by the prefix and suffix's
  --input-height: #{rem(24 + 8 + 8)};

  @include media-breakpoint-up(sm) {

    --input-height: #{rem(24 + 12 + 12)};
    margin-bottom: rem(48);
  }

  // Data prefix and suffix
  .form-control__wrapper {
    display: inline-block;
    width: 100%;
    position: relative;
  }

  .form-control__wrapper[data-prefix]:before,
  .form-control__wrapper[data-suffix]:before {
  
    content: attr(data-prefix);
    display: block;
    position: absolute;
    text-align: center;
    bottom: 1px;
    width: rem(48);
    left: 0;
    font-size: rem(20);
    pointer-events: none;
    @include var(color,--colour-muted);
    font-weight:  400;
    @include var(font-family, --font-heading);


    line-height: var(--input-height);
    height: var(--input-height);

    @if $compatible == 'true'{
      height: #{em(48,20)};
      line-height: #{em(48,20)};

      @include media-breakpoint-up(sm) {

        height: #{em(24 + 12 + 12,20)};
        line-height: #{em(24 + 12 + 12,20)};
      }
    }
  }

  @if $compatible == 'true'{
    &.form-group--sm .form-control__wrapper[data-prefix]:before,
    &.form-group--sm .form-control__wrapper[data-suffix]:before {
      @include media-breakpoint-up(sm) {

        height: #{rem(24 + 8 + 8)};
        line-height: #{rem(24 + 8 + 8)};
      }
    }

    &.form-group--lg .form-control__wrapper[data-prefix]:before,
    &.form-group--lg .form-control__wrapper[data-suffix]:before {
      @include media-breakpoint-up(sm) {

        height: #{rem(28 + 20 + 20)};
        line-height: #{rem(28 + 20 + 20)};
      }
    }
  }

  .form-control__wrapper[data-prefix],
  .form-control__wrapper[data-icon] {
    .form-control {
      padding-left: rem(48);
    }
  }

  .form-control__wrapper[data-suffix]{

    &:before {
      content: attr(data-suffix);
      left: auto;
      right: 0;
      width: rem(48 + 16);
    }

    .form-control {

      padding-right: rem(48 + 16);
    }
  }


  .form-control__wrapper[data-icon]{
    .svg__wrapper {

      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
    }
    .svg__wrapper svg {
        
      display: block;
      position: absolute;
      top: calc(50% - #{rem(24/2)});
      width: rem(24);
      height: rem(24);
      left: rem(16);
    }
  }

  &.form-group--inline-suffix .form-control__wrapper[data-suffix] {

    position: relative;
    
    .form-control {

      padding-right: rem(16);
    }


    &:before {
      left: calc(100% + #{rem(16)});
    }
  }

  // old stuff
  p:last-child{
    padding-bottom: 0;
  }

  > *:last-child {
    margin-bottom: 0;
  }
  
  // Hidden
  &--hidden {
    z-index: -99;
    position: absolute;
    opacity: 0;
    top: -99999px;
  }

  .form-control ~ .form-control {
    margin-left: calc(#{rem(8)} - 4px);
  }

  &--1-char .form-control {
    padding-left: 0;
    padding-right: 0;
    text-align: center;
  }
  &--1-char .form-control ~ .form-control {
    margin-left: calc(#{rem(4)} - 4px);

    @include media-breakpoint-up(sm) {

      margin-left: calc(#{rem(8)} - 4px);
    }
  }
}
// #endregion

// #region width variants
.form-group {
  
  @include input-widths($long: 560, $medium: 216, $short: 144, $four: 72, $two: 48, $one: 32, $init: 'true');

  @include media-breakpoint-up(sm) {
    @include input-widths($long: 560, $medium: 400, $short: 240, $four: 96, $two: 64, $one: 48);
  }
}
// #endregion

// #region Size varients
@include media-breakpoint-up(sm) {
  .form-group--sm {

    --input-height: #{rem(24 + 8 + 8)};

    margin-bottom: rem(40);

    label {

      font-size: rem(14);
    }
    .form-control {
      font-size: rem(14);
      padding: rem(8) rem(12);
      line-height: rem(24);
      @if $compatible == 'true'{
        height: rem(24 + 8 + 8);
      }
    }

    @include input-widths($long: 560, $medium: 216, $short: 144, $four: 72, $two: 48, $one: 32);
  }

  .form-group--lg {

    --input-height: #{rem(24 + 20 + 20)};
    margin-bottom: rem(48);
    
    label {

      font-size: rem(20);
      line-height: rem(28);
      margin-bottom: rem(16);
    }

    .form-control {
      font-size: rem(20);
      line-height: rem(28);
      @if $compatible == 'true'{
        height: rem(28 + 20 + 20);
      }
      padding: rem(20) rem(16);
    }

    @include input-widths($long: 800, $medium: 560, $short: 320, $four: 144, $two: 88, $one: 64);
  }
}
// #endregion

// #region Validation 
// We only show errors once a user has tried to submit form
.form--submitted {

  .form-group--error {
    padding-left: rem(16);
    border-left: 4px solid currentColor;
    @include var(border-color, --colour-danger);
    
    .form-control, .form-control:invalid, .form-control.is-invalid, .form-control.input-validation-error  {
      
      @include var(border-color, --colour-danger);

      &.focus {
        box-shadow: 0 0 0 1px var(--colour-danger);
      }
    }
  

    .form-control {
      
      @include invert-colours(){

        @include var(border-color, --colour-danger);
      }
    }

    .error:not([aria-hidden="true"]) {
      display: block;
      @include var(color,--colour-danger);
      @include var(font-family,--font-body);
      font-weight: bold;
      font-variation-settings: 'wght' 600;
      font-size: rem(16);
      padding-top: rem(8);
    }

    .error + .error {
      display: none!important;
    }
  }

  .field-validation-error,
  .error {
    display: none;

    &[aria-hidden="true"] {
      display: none;
    }

    &:not([aria-hidden="true"]) {
      display: block;
    }
  }
}
// #endregion

// #region Decorative text input 
.form-group--decorative {

  .form-control {
    border: none;
    border-bottom: 1px solid currentColor;
    background-color: transparent;
    padding-left: 0;

    &.hover {

      box-shadow: none;
    }

    &.focus,
    &.active {
      background-color: transparent;
      box-shadow: none;
      border-bottom: 2px solid currentColor;
      margin-bottom: -1px;
    }

    &[disabled] {
      
      @include var(border-bottom-color, --colour-border);
    }

  }
}
.form--submitted .form-group--decorative.form-group--error {

  border-left: none;
  padding-left: 0;

  .form-control {
    border-bottom: 2px solid currentColor;
    @include var(border-color,--colour-danger);
    margin-bottom: -1px;

    &.focus, 
    &.active {

      box-shadow: none;
    }
  }
}

// #endregion

// #region Textarea
textarea,
textarea.form-control {
  min-height: 6rem;
  max-height: 25vh;
}
// #endregion

// #region Stepper 
.form-group--stepper {

  &:not(.form-group--long):not(.form-group--medium):not(.form-group--short):not(.form-group--4-chars):not(.form-group--2-chars) {
    
    --input-max-width: var(--input-width-two);
  }

  &[data-prefix]:not(.form-group--long):not(.form-group--medium):not(.form-group--short):not(.form-group--4-chars):not(.form-group--2-chars), 
  &[data-suffix]:not(.form-group--long):not(.form-group--medium):not(.form-group--short):not(.form-group--4-chars):not(.form-group--2-chars) {
    
    --input-max-width: calc(var(--input-width-four) + 3rem);

    @if $compatible == 'true' {

        .form-control,
        .form-control__wrapper {
          max-width: rem(144)
        }
    }
  }

  &.form-group--inline-suffix .form-control__wrapper:before{
    margin-left: rem(16 * 3);
  }

  // IE11 fix for the default width
  @if $compatible == 'true' {

    &:not(.form-group--long):not(.form-group--medium):not(.form-group--short):not(.form-group--4-chars):not(.form-group--2-chars) .form-control {
      max-width: rem(64)
    }
  }

  .form-control,
  .form-control__wrapper {
    float: left;
  }

  .form-control__wrapper {
    width: calc(100% - 6rem);
    margin-left: rem(8);
    position: relative;
  }

  .form-control.stepper__minus,
  .form-control.stepper__plus {

    width: var(--input-height);
    @if $compatible == 'true' {

      width: rem(48)!important;
    }
    text-indent: -300%;
    overflow: hidden;
    position: relative;
    @include var(background,--colour-light);
    @include var(border-color,--colour-border);
    cursor: pointer;
    padding-left: 0;

    @include invert-colours(){
      background: transparent;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      padding: 0!important;
      text-align: center;
      font-size: rem(48);
      vertical-align: middle;
      box-sizing: content-box;
      @include var(color,--colour-body-light);
      @include prefix(user-select, none);

      @if $compatible == 'true' {
    
        background-image: escape-svg($plus-svg);
        background-size: rem(32) rem(32);
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-color: transparent!important;

        @include invert-colours(){
          background-image: escape-svg($plus-white-svg);
        }
      }
      @else {
        background: currentColor;
        mask-image: var(--icon-plus);
        mask-size: rem(32) rem(32);
        mask-repeat: no-repeat;
        mask-position: 50% 50%;
      
        -webkit-mask-image: var(--icon-plus);
        -webkit-mask-size: rem(32) rem(32);
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-position: 50% 50%;


        @include invert-colours(){
          background: var(--colour-white);
        }
      }
    }

    &.hover,
    &.focus {
      @include var(border-color,--colour-muted);

      @if $compatible == 'true' {
        box-shadow: 0 0 0 1px #6D7173;
      }
      @else {
        box-shadow: 0 0 0 1px var(--colour-muted);
      }
    }

    &.active {
      @include var(border-color,--colour-body);

      @if $compatible == 'true' {
        box-shadow: 0 0 0 1px #000000;
      }
      @else {
        box-shadow: 0 0 0 1px var(--colour-body);
      }
    }
  }

  .bg-light & {
    .form-control.stepper__minus,
    .form-control.stepper__plus {
      @include var(border-color,--colour-muted);
    }
  }

  .form-control.stepper__minus:before {
    @if $compatible == 'true' {
  
      background-image: escape-svg($minus-svg);

      @include invert-colours(){
        background-image: escape-svg($minus-white-svg);
      }
    }
    @else {
      mask-image: var(--icon-minus);
      -webkit-mask-image: var(--icon-minus);
    }
  }

  // Change icon colour when on a dark background and active
  .form-control.stepper__minus.focus:before,
  .form-control.stepper__minus.active:before {
    @include invert-colours(){

      @if $compatible == 'true' {
    
        background-image: escape-svg($minus-svg);
      }
      @else {
        background: var(--colour-body);
      }
    }
  }

  .form-control.stepper__plus.focus:before,
  .form-control.stepper__plus.active:before {
    @include invert-colours(){
      
      @if $compatible == 'true' {
    
        background-image: escape-svg($plus-svg);
      }
      @else {
        background: var(--colour-body);
      }
    }
  }

  input.form-control {
    
    text-align: center;
  }

  .form-control.stepper__plus {
    margin-right: 0;
  }
}
// #endregion

// #region Form group inline
.form-group--inline {

  display: flex;
  flex-wrap: wrap;
  
  label {
    min-width: rem(176);
    flex: 1 0 rem(176);
    margin-bottom: 0;
    margin-right: rem(16);
    padding-top: rem(12);
  }

  input, select {

    flex: 1 1 calc(100% - #{rem(176 + 16)});
    max-width: calc(100% - #{rem(176 + 16)});
    margin: 0;
  }

  .form-control__wrapper {

    flex: 1 1 calc(100% - #{rem(176 + 16)});
    //max-width: calc(100% - #{rem(176 + 16)});
    text-align: right;
  }

  &[data-prefix]:before {
    
    left: rem(192);
    
  }
}


// #endregion

// #region Text center fixes 
.text-center {
  .form-group {

    margin-left: auto;
    margin-right: auto;
    
    h5,
    label {
      text-align: left;
    }
  }
}
// #endregion

// #region Dark background fixes 
.form-group {
  @include invert-colours(){

    --colour-danger: #ffffff;

    .form-control {

      @include var(border-color, --colour-border);
      @include var(color, --colour-white);

      &.hover,
      &.active,
      &.focus {
        --border-colour: var(--colour-white);
        @include var(border-color, --colour-white);
        
        @if $compatible == 'true' {
          box-shadow: 0 0 0 1px #ffffff;
        }
        @else {
          box-shadow: 0 0 0 1px var(--border-colour);
        }
      }

      &.active,
      &.focus {
        @include var(background-color, --colour-white);
        @include var(color, --colour-body);
      }

      &[disabled] {
        background-color: transparent;
        @include var(border-color, --colour-border);
        opacity: 0.4;
      }
    }
  }

  &.form-group--decorative {
    @include invert-colours(){
      .form-control {

        @include var(border-color,--colour-muted);

        &.hover {
          box-shadow: none;
          @include var(border-color,--colour-white);
        }

        &.focus,
        &.active {
          background-color: transparent;
          box-shadow: none;
          @include var(color,--colour-white);
          @include var(border-color,--colour-white);
        }
      }
    }
  }
}

.form--submitted {
  @include invert-colours(){
    .form-group--decorative.form-group--error .form-control {

      @include var(border-color,--colour-white);
    }
  }
}
// #endregion

// #region Search
.form-control[type="search"] {
  padding-right: rem(40);
}
.form-control[type="search"] + button {

  border: none;
  overflow: hidden;
  height: rem(24);
  width: rem(24);
  background: none;
  outline: none;

  text-indent: -300px;
  position: absolute;
  bottom: calc(50% - #{rem(24/2)});
  right: rem(8);


  @include var(color,--colour-muted);

  &.hover,
  &.active {
    outline: none;
  }

  &.hover:not(.active),
  &.focus:not(.active) {
    @include var(color,--colour-black);
  }

  &.active  {
    @include var(color,--colour-active);
  }

  @include search-icon(24);
}
// #endregion

// #region Password
.form-group--password {

  .form-control {
    padding-right: rem(100);
  }
  .form-control__wrapper button {
    position: absolute;
    top: 1px;
    right: 1px;
    height: calc(100% - 2px);
    line-height: 1;
    padding: 0 1rem;
    border-left: 1px solid var(--colour-border);
    @include var(background,--colour-light);
    min-width: rem(80);
    text-align: center;

    &.hover:not(.active),
    &.focus:not(.active) {
      outline: none;
      border: 1px solid var(--colour-muted);
      box-shadow: 0 0 0 1px var(--colour-muted)!important;
    }
    
    &.active {
      outline: none;
      border: 1px solid var(--colour-body);
    }
  }
}
// #endregion