// #region Default table
table {
  width: 100%;
  font-size: rem(16);
  border: none;
  margin-bottom: rem(32);
  display: block;
  overflow: auto;
}

td,th {
  padding: rem(16);
  font-weight: normal;
  text-align: left;
  vertical-align: top;
  border: none;
  font-size: rem(16);
  line-height: rem(24);
}

th {
  font-weight: bold;
  font-variation-settings: 'wght' 600;
}

tr {
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);

  &:last-child {
    border-bottom: 2px solid currentColor;
    @include var(border-color,--colour-border);
  }
  
  @include invert-colours(){
    
    @include var(color,--colour-white);
    @include var(border-color,--colour-white);
  }

  .bg-dark-2 & {

    @include var(border-color,--colour-muted,!important);
  }
}

thead {
  tr:first-child {
    border-top: 2px solid currentColor;
    @include var(border-color,--colour-border);
  }

  th {
    font-size: rem(16);
    line-height: rem(24);
    padding-bottom: rem(24);
    color: inherit;
  
    @include media-breakpoint-up(sm) {
  
      font-size: rem(18);
    }


    font-weight: bold;
    display: table-cell;
    width: auto;
    vertical-align: bottom;
  
    @include media-breakpoint-up(sm) {
  
      width: auto;
      display: table-cell;
    }
  }
}

tbody:first-child{
  tr:first-child {
    border-top: 2px solid currentColor;
    @include var(border-color,--colour-border);
  }
}
// #endregion

// #region Table wrapper - Add via JS if needed, provides a safe space to scroll
.table__wrapper {
  display: block;
  width: calc(100% + 1rem);
  padding-right: 1rem;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar; // See https://github.com/twbs/bootstrap/pull/10057
  margin-bottom: rem(32);


  table {
    margin-bottom: 0;
    display: table;
    overflow: hidden;
  }
}
// #endregion

// #region Tables are only stacked on mobile
@media (max-width: 36em) {
  .table-stacked {
    thead {
      display: block;
    }

    tbody {
      display: block;

      border-top: 2px solid currentColor;
      @include var(border-color,--colour-border);

      @include invert-colours() {
        @include var(color,--colour-white);
        @include var(border-color,--colour-muted);
      }
    }

    tr {
      display: block;
      position: relative;
      padding-left: 0.5rem;
      padding-right: 0.5rem;
      padding-bottom: 1rem;
      padding-top: 1rem;
    }

    td,
    th {
      display: block;
      padding: 0.5rem 0rem;

      @include invert-colours() {
        @include var(color,--colour-white);
        @include var(border-color,--colour-muted);
      }
    }

    tr, td, th {

      &:after {
        content:"";
        clear: both;
        display: block;
      }
    }
  }

  .js-enabled .table-stacked {
    thead {
      display: none;
    }
    td,
    th {
      display: block;
      padding: 0.5rem 0rem;
      padding-left: 50%;

      @include invert-colours() {
        @include var(color,--colour-white);
        @include var(border-color,--colour-muted);
      }

      &[data-col]:before {
        content: attr(data-col);
        
        padding: 0;
        margin-bottom: 0;
        display: inline-block;
        width: 100%;
        font-weight: bold;
        margin: 0;
        margin-left: -100%;
        padding-right: 1rem;
        vertical-align: top;
      }
    }
  }
}
// #endregion

// #region Table striped
.table-striped tbody tr:nth-of-type(odd) {

  @include var(background-color,--colour-light);
  
  @include invert-colours(){
  
    @include var(color,--colour-white);
    background-color: rgba(255,255,255,0.1);
  }

  .bg-light & {

    @include var(background-color,--colour-white);
  }
}
// #endregion

// #region Table hover
.table-hover tbody tr {
  &.hover,
  &.focus {
    @include var(background-color,--colour-light);

    @include invert-colours(){
      @include var(color,--colour-white);
      background-color: rgba(255,255,255,0.1);
    }
  }

  .bg-light &.hover,
  .bg-light &.focus {
    @include var(background-color,--colour-white);
  }
}
// #endregion