// #region Dropdown selects
.custom-select {
  
  @extend .form-control;
  border-radius: 0;
  display: block;
  width: 100%;

  @include prefix(appearance,none,webkit moz);

  @if $compatible == 'true' {
  
    background-image: escape-svg($chevron-down-svg);
  }
  @else {
    background-image: var(--icon-chevron-down);
  }

  background-size: rem(32) rem(32)!important;
  background-position: calc(100% - #{rem(8)}) 50%;
  background-repeat: no-repeat;
  box-shadow: none;

  @include invert-colours(){
    
    &:not(.active):not(.focus){
      @if $compatible == 'true' {
    
        background-image: escape-svg($chevron-down-white-svg);
      }
      @else {
        background-image: var(--icon-chevron-down-white);
      }
    }
  }

  &[disabled] {

    @include var(background-color, --colour-light);
    @include var(border-color, --colour-border);
  }

  &.focus {
    
    outline: 0;
    
    &::-ms-value {
      // For visual consistency with other platforms/browsers,
      // suppress the default white text on blue background highlight given to
      // the selected option text when the (still closed) <select> receives focus
      // in IE and (under certain conditions) Edge.
      // See https://github.com/twbs/bootstrap/issues/19398.
      color: $input-color;
      background-color: $input-bg;
    }
  }

  &.active {
    @include var(background-color, --colour-light);
  }

  // Hides the default caret in IE11
  &::-ms-expand {
    display: none;
  }

  &[multiple],
  &[size]:not([size="1"]) {
    height: auto;
    //padding-right: $custom-select-padding-x;
    background-image: none;
  }
}

.form-group--error .custom-select {
  
  @include var(border-color,--colour-primary);

  &.hover,
  &.focus,
  &.active {
    @include var(border-color,--colour-primary);
  }
}
.form-group.form-group--decorative {
  .custom-select {
    @include invert-colours(){
      
      
      &.focus,
      &.active {
        @include var(color,--colour-body);
        @include var(background-color,--colour-white);
        padding-left: 0.5rem;
      }
    }
  }
}
// #endregion

// #region custom control - checkboxes and radio buttons
.custom-control {
  
  > input {
    position: absolute;
    z-index: -1; // Put the input behind the label so it doesn't overlay text
    opacity: 0;
  }
  
  padding-left: 0;
  margin-bottom: rem(16);
  @include clearfix();
  position: relative;

  @include media-breakpoint-up(sm) {

    margin-bottom: rem(24);
  }
}

.custom-control-label {
  
  position: relative;
  padding-bottom: 0;
  padding-left: rem(32);
  margin-bottom: rem(16);

  // Colour variables so that we can easily update them for the dark background variant
  --custom-control-bg: var(--colour-white);
  --custom-control-border: var(--colour-muted);
  --custom-control-bg-disabled: var(--colour-light);
  --custom-control-border-disabled: var(--colour-border);
  --custom-control-border-hover: var(--colour-body);
  --custom-control-bg-active: var(--colour-light);
  // Checkbox
  --checkbox-border-checked: var(--colour-dark-2);
  --checkbox-bg-checked: var(--colour-dark-2);
  --checkbox-tick: var(--colour-white);
  --checkbox-border-checked-hover: var(--colour-body);
  --checkbox-border-checked-active: transparent;
  --checkbox-bg-checked-active: var(--colour-muted);
  --checkbox-tick-active: var(--colour-white);
  // Radio
  --radio-border-checked: var(--colour-body);
  --radio-bg-checked: var(--colour-white);

  // When on a dark background update the colour variables
  // IE11 fixes will be needed and will be located in each of the below rule blocks
  @include invert-colours(){

    --custom-control-bg: transparent;
    --custom-control-border: var(--colour-white);
    --custom-control-bg-disabled: transparent;
    --custom-control-border-disabled: var(--colour-white);
    --custom-control-border-hover: var(--colour-white);
    --custom-control-bg-active: var(--colour-white);
    // Checkbox
    --checkbox-border-checked: var(--colour-white);
    --checkbox-bg-checked: var(--colour-white);
    --checkbox-tick: #{$colour-body};
    --checkbox-border-checked-hover: var(--colour-muted);
    --checkbox-border-checked-active: var(--colour-border);
    --checkbox-bg-checked-active: transparent;
    --checkbox-tick-active: var(--colour-border);
  }

  @include media-breakpoint-up(sm) {

    padding-left: rem(36);
    //line-height: rem(32);
    margin-bottom: rem(24);
  }

  &:last-child {
    margin-bottom: 0;
  }

  // Background-color
  &:before {
    content: "";
    pointer-events: none;
    position: absolute;
    display: block;
    width: rem(24);
    height: rem(24);
    top: 0;
    left: 0;
    border: 1px solid currentColor;
    @include var-local(border-color,--custom-control-border,--colour-muted);
    @include var-local(background,--custom-control-bg,--colour-white);
    box-shadow: none;

    @include invert-colours-compatible(){
      background: transparent;
      @include var(border-color,--colour-white);
    }
  }

  // Foreground (icon)
  &:after {
    content: "";
    pointer-events: none;
    position: absolute;
    display: block;
    //background: no-repeat 50% / #{$custom-control-indicator-bg-size};
    width: rem(24);
    height: rem(24);
    top: 0;
    left: 0;
  }

  // Radio - Add border radius
  .custom-control-input[type="radio"] + &:before {
    //@include var(background-color, --colour-white);
    border-radius: 50%;
  }

  // Disabled
  .custom-control-input[type="checkbox"]:disabled ~ &:before,
  .custom-control-input[type="radio"]:disabled + &:before {

    @include var-local(background,--custom-control-bg-disabled, --colour-light);
    @include var-local(border-color,--custom-control-border-disabled, --colour-border);

    @include invert-colours(){
      opacity: 0.4;
    }

    @include invert-colours-compatible(){
      background: transparent;
      @include var(border-color,--colour-white);
    }
  }

  // Hover
  .custom-control-input[type="checkbox"]:not(:disabled) ~ &.hover:before, 
  .custom-control-input[type="radio"]:not(:disabled) + &.hover:before {

    @include var-local(border-color,--custom-control-border-hover,--colour-body);
    box-shadow: 0 0 0 1px var(--custom-control-border-hover);
    
    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-body)};
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-white)};
    }
  }

  // Focus
  .custom-control-input[type="checkbox"]:not(:disabled).focus ~ &:before,
  .custom-control-input[type="radio"]:not(:disabled).focus + &:before {

    @include var-local(border-color,--custom-control-border-hover,--colour-body);
    box-shadow: 0 0 0 1px var(--custom-control-border-hover);

    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-body)};
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-white)};
    }
  }

  // Active
  .custom-control-input[type="checkbox"]:not(:disabled).active ~ &:before,
  .custom-control-input[type="radio"]:not(:disabled).active + &:before {

    @include var-local(border-color,--custom-control-border-hover,--colour-body);
    @include var-local(background,--custom-control-bg-active,--colour-light);

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      @include var(background,--colour-white);
    }
  }

  // Checkbock CHECKED
  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ &:before {

    @include var-local(border-color,--checkbox-border-checked,--colour-dark-2);
    @include var-local(background,--checkbox-bg-checked,--colour-dark-2);

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      @include var(background,--colour-white);
    }
  }

  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ &:after {

    @if $compatible == 'true' {

      background-image: escape-svg($tick-white-svg);
      background-size: rem(24) rem(24);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-color: transparent!important;
    }
    @else {
      mask-image: var(--icon-tick);
      mask-size: rem(24) rem(24);
      mask-repeat: no-repeat;
      mask-position: 50% 50%;
    
      -webkit-mask-image: var(--icon-tick);
      -webkit-mask-size: rem(24) rem(24);
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-position: 50% 50%;
      @include var-local(background,--checkbox-tick,--colour-white);
    }

    @include invert-colours-compatible(){
      background-image: escape-svg($tick-svg);
    }
  }

  // Checkbock CHECKED & hover,focus
  .custom-control-input[type="checkbox"]:not(:disabled):checked.focus ~ &:before,
  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ &.hover:before {

    @include var-local(border-color,--checkbox-border-checked-hover,--colour-body);
    box-shadow: 0 0 0 1px var(--checkbox-border-checked-hover);

    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-body)};
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-muted);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-muted)};
    }
  }
  
  // Checkbock CHECKED & active
  .custom-control-input[type="checkbox"]:not(:disabled):checked.active ~ &:before {

    @include var-local(border-color,--checkbox-border-checked-active,transparent);
    @include var-local(background,--checkbox-bg-checked-active,--colour-muted);
    box-shadow: none;

    @include invert-colours-compatible(){
      @include var(border-color,var(--colour-border));
      background: transparent;
      box-shadow: none;
    }
  }
  .custom-control-input[type="checkbox"]:not(:disabled):checked.active ~ &:after {

    @if $compatible == 'false' {

      @include var-local(background,--checkbox-tick-active,--colour-white);
    }

    @include invert-colours-compatible(){

      background-image: escape-svg($tick-white-svg);
    }
  }

  // Radio CHECKED
  .custom-control-input[type="radio"]:not(:disabled):checked + &:before {

    @include var-local(background,--radio-bg-checked,--colour-white);
    @include var-local(border-color,--radio-border-checked,--colour-body);


    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      @include var(background,--colour-white);
    }
  }

  .custom-control-input[type="radio"]:not(:disabled):checked + &:after {

    width: rem(12);
    height: rem(12);
    margin-left: #{rem(((24 - 12)/2))};
    margin-top: #{rem(((24 - 12)/2))};;
    @include var(background-color, --colour-body);
    border-radius: 50%;
  }

  // Radio CHECKED & hover,focus
  .custom-control-input[type="radio"]:not(:disabled):checked.focus + &:before,
  .custom-control-input[type="radio"]:not(:disabled):checked + &.hover:before {

    @include var-local(border-color,--checkbox-border-checked-hover,--colour-body);
    box-shadow: 0 0 0 1px var(--checkbox-border-checked-hover);

    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-body)};
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-muted);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-muted)};
    }
  }

}

// #endregion

// #region two columnns custom controls

@include media-breakpoint-up(sm) {
    .custom-control--two-columns {

        display: flex;
        flex-flow: row wrap;

        label.custom-control-label.btn {
            width: calc(50% - #{rem(8)});
            max-width: calc(50% - 0.5rem);
            flex-shrink: 1;
            display: inline-block;
            min-width: calc(50% - 0.5rem);

            &:nth-of-type(odd) {
                margin-right: rem(16);
            }
        }

        .option-content {
            max-width: 100%;
            min-width: 100%;
        }
    }
}
@include media-breakpoint-up(md) {
    .custom-control--md-two-columns {

        display: flex;
        flex-flow: row wrap;

        label.custom-control-label.btn {
            width: calc(50% - #{rem(8)});
            max-width: calc(50% - 0.5rem);
            flex-shrink: 1;
            display: inline-block;
            min-width: calc(50% - 0.5rem);

            &:nth-of-type(odd) {
