// #region Checkbox as button
.custom-control-label.btn {

  &:not(.btn-secondary) {

    --custom-btn-bg: var(--colour-light);
    --custom-btn-border: var(--colour-border);
    --custom-btn-border-hover: var(--colour-muted);
    --custom-btn-border-active: var(--colour-body);
  }

  &.btn-secondary.active {

    --custom-btn-bg: var(--colour-light);
    --custom-btn-border: var(--colour-border);
    --custom-btn-border-hover: var(--colour-muted);
  
    color: var(--btn-colour-active);
    background: var(--btn-bg-active);
    border-color: var(--btn-border-active);
  }

  --custom-btn-bg-checked: var(--colour-dark-2);
  --custom-btn-color-checked: var(--colour-white);
  --custom-btn-border-checkbox-checked: var(--colour-white);
  --custom-btn-border-checkbox-hover: var(--colour-white);


  @include invert-colours(){

    --custom-btn-bg: transparent;
    --custom-btn-border: var(--colour-white);
    --custom-btn-border-hover: var(--colour-white);
    --custom-btn-border-active: var(--colour-white);
    --custom-btn-bg-checked: var(--colour-white);
    --custom-btn-color-checked: #{$colour-body};
    --custom-btn-border-checkbox-checked: var(--colour-body);
    --custom-btn-border-checkbox-hover: var(--colour-muted);

    @if $compatible == 'true' {

      background: transparent;
      @include var(border-color,--colour-white);
    }
  }

  @include var-local(background,--custom-btn-bg,--colour-light);
  border: 1px solid currentColor;

  @include var-local(border-color,--custom-btn-border,--colour-border);

  padding: rem(12) rem(12) rem(12) rem(12 + 24 + 12);
  margin-bottom: rem(8);
  display: block;
  

  @include media-breakpoint-up(sm) {
    padding: rem(16) rem(16) rem(16) rem(16 + 24 + 16);
    margin-bottom: rem(16);
  }

  .custom-control-input[type="checkbox"]:not(:checked) ~ &.btn-secondary,
  .custom-control-input[type="radio"]:not(:checked) + &.btn-secondary {
    padding-right: rem(12);
    
    @include media-breakpoint-up(sm) {
      padding-right: rem(16);
    }
  }

  .custom-control-input[type="radio"]:not(:disabled) + &:not(.btn) {

    padding-left: rem(56);
    padding-right: rem(24);
  }

  &:before,
  &:after {
    top: rem(12);
    left: rem(12);

    @include media-breakpoint-up(sm) {
      top: rem(16);
      left: rem(16);
    }
  }

  // Hover
  .custom-control-input[type="checkbox"]:not(:disabled) ~ &.hover,
  .custom-control-input[type="radio"]:not(:disabled) + &.hover,
  .custom-control-input[type="checkbox"]:not(:disabled).focus ~ &,
  .custom-control-input[type="radio"]:not(:disabled).focus + &  {
    
    @include var-local(border-color,--custom-btn-border-hover, --colour-muted);
    box-shadow: 0 0 0 1px var(--custom-btn-border-hover) !important;

    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-muted)};
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-white)};
    }
  }

  // Active
  .custom-control-input[type="checkbox"]:not(:disabled).active ~ &,
  .custom-control-input[type="radio"]:not(:disabled).active + &  {

    @include var-local(border-color,--custom-btn-border-active, --colour-body);
    box-shadow: 0 0 0 1px var(--custom-btn-border-active) !important;
    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-body)} !important;
    }

    @include invert-colours-compatible(){
      @include var(border-color,--colour-white);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-white)} !important;
    }
  }

  // Checked
  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ &,
  .custom-control-input[type="radio"]:not(:disabled):checked + & {
    @include var-local(background-color,--custom-btn-bg-checked, --colour-dark-2);
    @include var-local(color,--custom-btn-color-checked, --colour-white);

    @include invert-colours-compatible(){
      @include var(color, --colour-body);
      @include var(background, --colour-white);
    }

    &:before {
      @include invert-colours-compatible(){
        @include var(border-color, --colour-body);
      }
    }
  }

  .custom-control-input[type="checkbox"]:not(:disabled):not(.focus):not(.active):checked ~ &:not(.hover):before {
    @include var-local(border-color,--custom-btn-border-checkbox-checked,--colour-white);

    @include invert-colours-compatible(){
      
      @include var(border-color, --colour-body);
    }
  }

  .custom-control-input[type="checkbox"]:not(:disabled).focus:not(.active):checked ~ &:before,
  .custom-control-input[type="checkbox"]:not(:disabled):checked ~ &.hover:before {
    
    @include var-local(border-color,--custom-btn-border-checkbox-hover, --colour-muted);
    box-shadow: 0 0 0 1px var(--custom-btn-border-checkbox-hover);

    @if $compatible == 'true' {

      box-shadow: 0 0 0 1px #{map-get($vars, --colour-muted)};
    }

    @include invert-colours-compatible(){
      
      @include var(border-color, --colour-muted);
      box-shadow: 0 0 0 1px #{map-get($vars, --colour-muted)};
    }
  }
}

