// #region conditional content
.option-content {

  clear: both;
  display: none;
  margin-left: rem(8);
  padding-left: rem(20);


  @include media-breakpoint-up(sm) {
    
    padding-left: rem(24);
  }

  border-left: 4px solid currentColor;
  @include var(border-color,--colour-border);
  overflow: hidden;
  margin-bottom: rem(32);

  > *:last-child {
    margin-bottom: 0;
  }

  .form-group:not(.form-group--btns-inline) label {
    float: none;
  }
}

.option-1:checked ~ .option-1-content,
.option-2:checked ~ .option-2-content,
.option-3:checked ~ .option-3-content,
.option-4:checked ~ .option-4-content,
.option-5:checked ~ .option-5-content {
  display: block;
  overflow: visible;

  * {
    animation: none!important;
  }
}
// #endregion


// #region sliders/range
@mixin range-thumb() {

  width: rem(40);
  height: rem(40);
  border: none;
  border-radius: 50%;
  box-shadow: none;
  appearance: none;
  @include var(background-color,--colour-secondary);
  background-image: escape-svg($slider-svg);
  background-size: rem(24);
  background-repeat: no-repeat;
  background-position: 50% 50%;

  &.hover,
  &.focus {
    @include var(background-color,--colour-secondary-light);
  }

  &.active {
    @include var(background-color,--colour-secondary-tint);
  }
}

@mixin range-track() {
  width: 100%;
  height: rem(4);
  color: transparent; // Why?
  cursor: pointer;
  border-color: transparent;
  border-radius: 0;
  box-shadow: none;
  @include var(background-color,--colour-border);
}

.form-group--range {
  margin-bottom: rem(56 + 16 + 64 + 16 + 8);
  position: relative;
  z-index: 1;
}

input[type=range].custom-range {
  position: absolute;
  top: 100%;
  left: 0;
  width: calc(100% + #{rem(40)});
  max-width: calc(100% + #{rem(40)});
  flex: 1 0 calc(100% + #{rem(40)})!important;
  margin-top: rem(16 + 8);
  margin-left: rem(-20);
  margin-right: rem(-20);
  height: rem(40);
  padding: 0; // Need to reset padding
  background-color: transparent;
  appearance: none;
  -webkit-appearance: none;

  .form-group--error & {

    margin-left: calc(#{rem(-20)} - 4px);
    max-width: calc(100% + #{rem(40)} + 4px);
  }

  &.focus {
    outline: none;
    &::-webkit-slider-thumb { box-shadow: none; }
    &::-moz-range-thumb     { box-shadow: none; }
    &::-ms-thumb            { box-shadow: none; }
  }

  &::-moz-focus-outer {
    border: 0;
  }

  &::-webkit-slider-thumb {
    @include range-thumb();
    margin-top: rem(-18); // Webkit specific
    -webkit-appearance: none;
  }

  &::-webkit-slider-runnable-track {
    @include range-track();
    background: linear-gradient(to right, var(--bg-colour), var(--bg-colour) calc(#{rem(20)} - 1px), var(--colour-secondary) calc(#{rem(20)} - 1px),var(--colour-secondary) var(--percent,0%),var(--colour-border) var(--percent,0%),var(--colour-border) calc(100% - #{rem(20)} + 1px), var(--bg-colour) calc(100% - #{rem(20)} + 1px));
  }

  &::-moz-range-thumb {
    @include range-thumb();
  }

  &::-moz-range-track {
    @include range-track();
    background: linear-gradient(to right, var(--bg-colour), var(--bg-colour) calc(#{rem(20)} - 1px), var(--colour-secondary) calc(#{rem(20)} - 1px),var(--colour-secondary) var(--percent,0%),var(--colour-border) var(--percent,0%),var(--colour-border) calc(100% - #{rem(20)} + 1px), var(--bg-colour) calc(100% - #{rem(20)} + 1px));
  }

  &::-ms-thumb {
    @include range-thumb();
    margin-top: 0; // Edge specific
  }

  &::-ms-track {
    @include range-track();
  }

  &::-ms-fill-lower {
    @include var(background-color,--colour-secondary);
  }

  &::-ms-fill-upper {

    
    @include var(background-color,--colour-border);
  }

  &:disabled {
    opacity: 0.5;
  }
}

.custom-range__markers {
   
  height: rem(56);
  width: 100%;
  position: absolute;
  top: 100%;
  margin-top: rem(16);
  left: 0;
  z-index: -1;

  .form-group--error & {

    margin-left: -4px;
  }

  .marker {
    position: absolute;
    top: 0;
    height: 100%;
    display: none;

    &:before {
      content: "";
      display: block;
      top: 50%;
      left: 0;
      height: rem(16);
      margin-top: rem(-8);
      margin-left: -1px;
      width: 2px;
      position: absolute;
      @include var(background, --colour-border);
    }

    &__title {
      position: absolute;
      top: 100%;
      left: 0;
      transform: translate(-50%,0);
      opacity: 1;
    }

    &:first-child {

      display: block;

      .marker__title {
        transform: translate(-15%,0);
      }
    }

    &:last-child {
      display: block;
      width: 0!important;

      .marker__title {
        transform: translate(-85%,0);
      }
    }

  }


  @include media-breakpoint-up(sm) {
    .marker {
      display: block;
    }
  }
}

.form-group--range-highlight {
  @include media-breakpoint-up(sm) {

    .marker--highlight:after {
      content: "";
      display: block;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      position: absolute;
      @include var(background, --colour-secondary-light);
      opacity: 0.1;
    }
    
    .marker--highlight + .marker--highlight{
      background: transparent;
    }
  }
}

// #endregion
